export default class Cidade {
  constructor(axios, url) {
    this.url = `${url}cidade`
    this.axios = axios
  }
  async findByIdEstado (id) {
    try {
      let cidade = {
        estado: {
          id: id
        }
      }
      let response = await this.axios.post(`${this.url}/filtro`, cidade)
      return response.data
    } catch (err) {
      console.log({id})
      console.log({err})
    }
  }
}